const links = document.querySelectorAll('.app-tab');
const tabs = document.querySelectorAll('.tab');

links.forEach(link => {
    link.addEventListener('click', onTabClickHandler)
});


function onTabClickHandler(e) {
    e.preventDefault();

    links.forEach(el => el.classList.remove('active-link'));
    tabs.forEach(tab => tab.classList.add('hide'));
    this.classList.add('active-link');
    
    const el = document.querySelector( this.dataset.link );
    el.classList.remove('hide');
}