const BookStorage = (function() {
    let instance;
    
    function BookStorage() {
        if(!instance) instance = this;
        else return instance;
    }

    BookStorage.prototype.list = new Map();

    BookStorage.prototype.setStorage = function() {
        localStorage.setItem('book_storage', JSON.stringify({}));
    }

    BookStorage.prototype.get = function(isArr) {
        const books = JSON.parse( localStorage.getItem('book_storage') );
        if(isArr) return Object.values(books).reverse();
        else return books;
    }

    BookStorage.prototype.addToStorage = function(value) {
        const tmp = JSON.parse( localStorage.getItem('book_storage') );
        tmp[value.id] = value;
        localStorage.setItem('book_storage', JSON.stringify(tmp));
    }

    BookStorage.prototype.removeFromStorage = function(value) {
        const tmp = JSON.parse( localStorage.getItem('book_storage') );
        delete tmp[value.id];
        localStorage.setItem('book_storage', JSON.stringify(tmp));
    }

    BookStorage.prototype.isStorage = function() {
        return localStorage.getItem('book_storage');
    }
    return BookStorage;
})();

const bookStorage = new BookStorage();

//Set Book Storage
if( !bookStorage.isStorage() ) {
    bookStorage.setStorage();
}

export default bookStorage;