import bookStorage from './book_storage';

class Book {
    //The prefix must contain a hyphen (-) character at the end 
    static template(info, prefix = '') {
        return `
            <div class="book-component flex wow" data-prefix="${prefix}" id='${prefix}${info.id}'>
                <div class="book-thumb">
                    <img src='${info.thumb}' draggable='false' alt=''/>
                    <div style='background: url("${info.thumb}")' class='book-overlay'></div>
                </div>
                <div class="book-info flex">
                    <h2 class="book-name">${info.title}</h2>
                    <p class="book-author">${info.authors}</p>
                    <p class="book-description">${info.description}</p>
                    <div class="search-block__container flex">
                        <a href="${info.link}" target="_blank" class="search-block__link book-read-link">
                            <span>Ознакомиться</span>
                            <div class="search-block__border border-1"></div>
                            <div class="search-block__border border-2"></div>
                        </a>
                        <a href="" class="flex book-btn ${info.lib ? "remove" : ""}">
                            <img src="icons/add.svg" draggable="false"/>
                            <img src="icons/remove.svg" draggable="false"/>
                        </a>
                    </div>
                </div>
            </div>
        `;
    }
    static events() {
        const bookContainers = document.querySelectorAll('.books-container');
        bookContainers.forEach(container => {
            container.addEventListener('click', containerBtnHandler )
        });

        function containerBtnHandler(e) {
            if( e.target.closest('.book-btn') ) {
                e.preventDefault();
                const parent = e.target.closest('.book-component');
                Book.addOrDelete(parent);
            }            
        }
    }
    static addOrDelete(el) {
        const regexp = new RegExp(el.dataset.prefix);
        const componentId = el.id.replace(regexp, '');
        const btn = el.querySelector('.book-btn');
        let component = bookStorage.list.get( componentId );
        
        if(!component) {
            component = bookStorage.get()[componentId];
        }

        component.lib = !component.lib;

        if(component.lib) {
            bookStorage.addToStorage(component);
            btn.classList.add('remove');
        } else {
            bookStorage.removeFromStorage(component);
            removeBtnClass(el);
        }
        
        Book.renderLib();
        

        function removeBtnClass(el) {
            const block = document.getElementById(componentId);
            if(block) {
                const btn = block.querySelector('.book-btn');
                btn.classList.remove('remove');
            }
        }
    }
    static renderLib() {
        const bookList = bookStorage.get(1);
        const libContainer = document.querySelector('.lib-container');
        let fragment = '';

        libContainer.innerHTML = '';

        bookList.forEach(book => {
            fragment += Book.template(book, 'lib-');
        });

        libContainer.insertAdjacentHTML('afterbegin', fragment);
    }
}

Book.events();

export default Book;